.test-case-maker-docs {
  position: fixed;
  top: 10px;
  right: 10px;
  background-color: var(--background-color);
  border-radius: 15px;
  max-width: calc(min(40em, 30vw));
  max-height: 28px;
  border: 1px solid var(--border-color);
  overflow: auto;
  padding: 10px;
  transition: max-height 0.25s;
  z-index: 1;
}
.test-case-maker-docs.open-docs {
  max-height: 90vh;
}
.test-case-maker-docs header {
  position: sticky;
  top: -11px;
  padding: 10px;
  z-index: 2;
  border-radius: 15px;
  border: 1px solid var(--border-color);
  border-top: none;
  background-color: var(--background-color);
  margin: -10px;
}
.test-case-maker-docs header h2 {
  margin-top: 0;
  margin-bottom: 0;
}
.test-case-maker-docs header:hover {
  background-color: var(--highlight-color);
  cursor: pointer;
}
