.light-dark-toggle {
  background-color: var(--background-color);
  border: 1px solid var(--border-color);
  padding: 10px;
  border-radius: 10px;
  margin: 10px;
  transition: background-color 0.25s;
  position: fixed;
  bottom: 10px;
  right: 10px;
}
.light-dark-toggle:hover {
  background-color: var(--highlight-color);
}
